import React, { Component } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import './About.scss';

class About extends Component {
    render() {
        return (
            <div className="wrap">
                <Header />
                <div className="about">
                    <h2>Our Company</h2>
                    <p>
                        This Corporation is based in Melbourne, Australia.<br />
                        Formed in 1997, Our Corporation have expanded its initial Domain Name business from New Zealand and Australia into a leading, world-wide Domain Registrar.<br />
                        We supply domain names in 250 countries worldwide and have direct accreditations with over 60 Registries.<br />
                        Many companies and household brand names from around the world take advantage of Instra’s unique 
                        and sophisticated <strong>“Reseller portal”</strong> to actively manage their online intellectual property.<br />
                        Our customers come from over 200 different countries.<br />
                        Company is active in ensuring we are constantly providing our customers with new domain name spaces, 
                        and are regularly updating our domain name offering from around the world.<br />
                        Company is first in ensuring we offer our valued corporate customers with knowledge about new spaces 
                        that are becoming available and the Registry processes required to secure these domain names.
                    </p>
                </div>
                <Footer />
            </div>
        );
    }
}

export default About;